a{
    text-decoration: none;
}
.card{
    cursor: pointer;
    max-width: 250px;
    max-height: 300px;
    overflow: hidden;
    border: 1px solid #ccc;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    text-align: center;
    margin: 10px;
}

.card:hover{
    box-shadow:  0 0 20px rgba(0,0,0,0.5);


    .cardName:hover{
        h2{
            color: #1D21D3;
            opacity: .6;
        }
    }
}

.cardImg{
   
    height: 14rem;
    img{
        width: 100%;
        height: 100%;
    }

}

.cardName{
    background: red;
    height: 3rem;

    h2{
        color: #fff;

    }

}